.sidebar{
    flex: 1;
   height: calc(100vh - 50px);
   background-color:rgb(238, 238, 238);
   position: sticky;
   top: 50px;
}
.sidebarwrapper{
    padding: 0 20px;
    color: #555;
}

.sidebarmenu{
margin-bottom: 10px;
}
.sidebarTitle{
    font-size: 14px;
    color: rgb(141, 139, 139);
}
.sidebarTitle.dashboard{
    font-size: 25px;
   
}
.sidebarlist{ 
    list-style: none;
    padding: 5px;
}
.sidebarlistitem{
    padding: 5px;
    cursor: pointer ;
    display: flex;
    align-items: center;
    border-radius: 10px;
    margin-top: 5px;
}
.sidebarlistitem.active , .sidebarlistitem:hover{
    background-color: rgb(228, 228, 250);
}
.sidebarIcon{
    margin-right: 5px ;
    font-size: 20px !important;
}
.Link{
   color: #555;
    text-decoration: none;
}